.pokemon-container {
    border: 1px solid black;

    color: green;
    font-size: 24px;
    text-align: center;
    width: 400px;
    height: 400px;
    background-color: #0d1617;
    margin: 0 auto;
}

.pokemon-image {
    width: 100%;
    height: 100%;
    border: 1px solid green;
    background-color: antiquewhite;
}

.rnd {
    background-color: azure;
    border: 1px solid yellow;
    border-radius: 12px;
}

.btn {
    width: 400px;
    height: 80px;
    padding: 10px;
    background-color: darkgreen;
    color: azure;
    font-size: 21px;
    border-radius: 11px;

}

.btn:hover {
    cursor: pointer;
}

.header {
    margin: 0 auto;
    color: #0ab682;
    width: 400px;
    text-align: center;
    height: 50px;
    padding: 20px 0;
    font-size: 33px;
}